"use client"
import { useAppDispatch } from "@/lib/global/hooks";
import { customerLogin } from "@/lib/slice/auth/cus.auth.slice";
import { IcusLogin } from "@/lib/slice/auth/cus.auth.types";
import { ChangeEvent, FormEvent, useState } from "react";
import { ToastContainer } from "react-toastify";
import Link from 'next/link'

const Login = () => {
  const dispatch = useAppDispatch()

  const [data, setData] = useState<IcusLogin>({
    email: "",
    password: ""
  })

  const onChangeData = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setData({
      ...data,
      [name]: value
    })

  }

  const onSubmitData = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    dispatch(customerLogin(data))

  }




  return <div className="bg-[url('/noodels.jpg')]  flex justify-center items-center bg-cover bg-center w-full h-[100vh] relative">
    <ToastContainer />
    <div className="container flex justify-center items-center bg-transparent">
      <div className="wrapper border-0 mt-2 h-[50%] w-[650px] ">
        <form className="  " onSubmit={onSubmitData}>
          <h2 className="font-bold text-5xl text-center mt-3">Sign In</h2>
          <div className="email mt-12 flex justify-center items-center rounded-2xl">
            <input className="text-center w-full h-[40px] outline-0 border-1 rounded-2xl text-2xl" type="email" placeholder="Email" name="email" value={data.email} onChange={onChangeData} />
          </div>
          <p className="flex justify-end my-2 hover:text-blue-500"><span>forgot password?</span></p>

          <div className="email mt-3  flex justify-center items-center">
            <input className="text-center w-full text-black h-[40px] rounded-2xl border-1 outline-0 text-2xl" type="password" placeholder="Password" name="password" value={data.password} onChange={onChangeData} />
          </div>

          <div className=" w-full mt-6 h-[50px] flex justify-center items-center ">
            <button className="bg-blue-500 w-full h-[45px] font-[500] text-[23px] rounded-[12px]">Login</button>
          </div>
          <label className="flex justify-center items-center mt-4 text-[18px]">Don't have an account?
            <Link href={'/auth/register'}>
              <span className="text-blue-500 ml-2 hover:underline cursor-pointer text-xl font-[500]">Register</span>

            </Link>
          </label>


        </form >
      </div>
    </div>





  </div >

}

export default Login
